.advantages__header-wrapper {
  padding: 0 20px;
  padding-top: 46px;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    padding: 0 20px;
    padding: 58px 20px 0 20px;
    margin-bottom: 42px;
  }

  @media (min-width: $desktop-width) {
    max-width: 960px;
    padding: 76px 10px 0 10px;
    margin: 0 auto;
    margin-bottom: 65px;
  }
}

.advantages__action {
  font-size: 24px;
  font-weight: lighter;
  line-height: 1.2;
  text-align: center;
  margin: 0;
  padding: 0;

  @media (min-width: $tablet-width) {
    font-size: 40px;
    line-height: auto;

    & br:nth-child(-n + 2) {
      display: none;
    }
  }

  @media (min-width: $desktop-width) {
    font-size: 50px;
  }
}

.advantages__container {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 0 20px;
  background-color: #f2f2f2;
  background-image: url('../img/bg-triangle-white.svg');
  background-repeat: no-repeat;
  background-size: contain;

  &::after {
    content: '';
    display: block;
    background-image: url('../img/iphone-screen-mobile@1x.png');
    background-size: cover;
    width: 157px;
    height: 277px;
    align-self: center;

    @media (min-resolution: 2dppx) {
      background-image: url('../img/iphone-screen-mobile@2x.png');
    }
  }

  @media (min-width: $tablet-width) {
    flex-direction: row-reverse;
    justify-content: center;

    &::after {
      background-image: url('../img/iphone-screen-3-tablet@1x.png');
      width: 268px;
      height: 482px;
      align-self: flex-end;
      margin-right: 42px;

      @media (min-resolution: 2dppx) {
        background-image: url('../img/iphone-screen-3-tablet@2x.png');
      }
    }
  }

  @media (min-width: $desktop-width) {
    padding: 0 10px;

    &::after {
      display: none;
    }
  }
}

// Через &(SCSS)

.advantages {
  // .advantages__list

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    padding-top: 85px;
    margin-bottom: 43px;
  }

  // .advantages__item

  &__item {
    text-align: center;
    margin-bottom: 43px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  // .advantages__title

  &__title {
    color: #000000;
    line-height: 24px;
    font-size: 18px;
    margin: 0;
    padding: 0;
    margin-bottom: 12px;
  }

  // .advantages__text

  &__text {
    font-size: 14px;
    line-height: 24px;
    margin: 0;
  }

  @media (min-width: $tablet-width) {
    &__list {
      max-width: 960px;
    }

    &__item {
      text-align: left;
      max-width: 310px;
      margin-bottom: 47px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &__text {
      & br {
        display: none;
      }
    }
  }

  @media (min-width: $desktop-width) {
    &__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      width: 100%;
      padding-top: 0;
      margin-bottom: 30px;
    }

    &__item {
      max-width: 252px;
      text-align: center;
      margin-bottom: 30px;

      &:nth-child(3n) {
        margin-right: 0px;
      }
    }

    &__title {
      font-size: 30px;
      line-height: 30px;
      margin-bottom: 35px;
    }

    &__text {
      font-size: 18px;
      line-height: 30px;
    }
  }
}

.advantages__item {
  position: relative;

  &-image--one {
    @media (min-width: $desktop-width) {
      &::before {
        content: '';
        display: block;
        margin-bottom: 50px;
        width: 100%;
        height: 300px;
        background-image: url('../img/iphone-screen-1-desktop@1x.png');
        background-repeat: no-repeat;
        background-size: 300px 100%;
        background-position: center 0;

        @media (min-resolution: 2dppx) {
          background-image: url('../img/iphone-screen-1-desktop@2x.png');
          background-size: 300px 100%;
        }
      }
    }
  }

  &-image--two {
    @media (min-width: $desktop-width) {
      &::before {
        content: '';
        display: block;
        margin-bottom: 50px;
        width: 100%;
        height: 300px;
        background-image: url('../img/iphone-screen-2-desktop@1x.png');
        background-repeat: no-repeat;
        background-size: 300px 100%;
        background-position: center;

        @media (min-resolution: 2dppx) {
          background-image: url('../img/iphone-screen-2-desktop@2x.png');
          background-size: 300px 100%;
        }
      }
    }
  }

  &-image--three {
    @media (min-width: $desktop-width) {
      &::before {
        content: '';
        display: block;
        margin-bottom: 50px;
        width: 100%;
        height: 300px;
        background-image: url('../img/iphone-screen-3-desktop@1x.png');
        background-repeat: no-repeat;
        background-size: 300px 100%;
        background-position: center;

        @media (min-resolution: 2dppx) {
          background-image: url('../img/iphone-screen-3-desktop@2x.png');
          background-size: 300px 100%;
        }
      }
    }
  }
}

